#cart {
  min-width: 100vw;
  position: absolute;
  top: 0;
  background-color: #f8f5f2;
}

#show-items {
  max-height: 60vh;
  position: relative;
  overflow-y: scroll;
}
.slide-right {
  animation-name: slide-right;
  animation-fill-mode: forwards;
  animation-duration: 0.8s;
}

.slide-left {
  animation-name: slide-left;
  animation-duration: 0.8s;
  animation-fill-mode: forwards;
}

@keyframes slide-right {
  from {
    left: 100vw;
  }
  to {
    left: 0vw;
  }
}

@keyframes slide-left {
  from {
    left: 0vw;
  }
  to {
    left: 100vw;
  }
}

.close-cart {
  text-align: end;
  margin: 4rem 4rem 0 0;
  cursor: pointer;
}

.block {
  display: block;
}

#cart-title,
#show-items {
  padding: 0 0 0 3rem;
}

#cart-title {
  font-weight: 600;
}

h1 {
  font-family: 'Montserrat SemiBold';
}

@media screen and (min-width: 750px) {
  #cart {
    min-width: 30vw;
  }

  @keyframes slide-right {
    from {
      left: 100vw;
    }
    to {
      left: calc(100vw - 30vw);
    }
  }

  @keyframes slide-left {
    from {
      left: calc(100vw - 30vw);
    }
    to {
      left: 100vw;
    }
  }
}
