* {
  --text-color: white;
  --primary-font: 'Montserrat', sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

input {
  all: unset;
  color: green;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
